    color: #4F4F4F;
    font-weight: normal;
}

.dami__contact-container_row__input-message textarea::placeholder {
    color: #4F4F4F;
    font-weight: normal;
}

.dami__contact-container button {
    width: 130px;
    min-height: 50px;
    font-family: var(--font-family);
    font-size: 20px;
    line-height: 27px;
    background: linear-gradient(90deg, #AE67FA 0%, #F49867 97.4%);
    padding: 0 1rem;
    border: none;
    border-radius: 5px;
    outline: none;
    cursor: pointer;
}
.dami__contact-container button p {
    margin-top: 0;
    color:#fff;
    font-weight: 500;
    font-size: 15px;
}

