        font-size: 48px;
        line-height: 60px;
    }

    .dami__header p {
        font-size: 16px;
        line-height: 24px;
    }

    .dami__header-content__input input,
    .dami__header-content__input button p {
        font-size: 16px;
        line-height: 24px;
    }

    .dami__header-image {
        padding: 0 3rem;
    }
}

@media screen and (max-width: 490px) { 
    .dami__header h1 {
        font-size: 36px;
        line-height: 48px;
    }

    .dami__header p {
        font-size: 14px;
        line-height: 24px;
    }

    .dami__header-content__input button {
        flex: 1 1;
    }

    .dami__header-content__input input,
    .dami__header-content__input button p {
        font-size: 12px;
        line-height: 16px;
    }
}
.dami__possibility {
    display: flex;
    flex-direction: row;
}

.dami__possibility-image {
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-right: 2rem;
}

.dami__possibility-image img {
    width: 100%;
}

.dami__possibility-content {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.dami__possibility-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 70px;
    line-height: 65px;
}

@media screen and (max-width: 1800px) { 
    .dami__possibility-content h1 {
        font-size: 50px;
        line-height: 55px;
    }
}

@media screen and (max-width: 950px) { 
    .dami__possibility {
        flex-direction: column;
    }

    .dami__possibility-image {
        margin: 1rem 0;
    }

    .dami__possibility-image img {
        width: 80%;
    }

    .dami__possibility-content {
        margin-top: 2rem;
    }

    .dami__possibility-content h1 {
        font-size: 50px;
        line-height: 35px;
    }

    .dami__possibility-content h1 {
        line-height: 55px;
    }
}

@media screen and (max-width: 550px) { 
    .dami__possibility-content h1 {
        font-size: 34px;
        line-height: 42px;
    }

}
.dami__projects {
    display: flex;
    flex-direction: column;
}

.dami__projects-heading {
    width: 100%;
    text-align: left;
    margin-bottom: 5rem;
}

.dami__projects-heading h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;
}

.dami__projects-container {
    display: flex;
    flex-direction: row;
}

.dami__projects-container_groupA {
    flex: 0.75 1;
    margin-right: 2rem;
}

.dami__projects-container_groupB {
    flex: 1 1;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
    gap: 2rem;
}

@media screen and (max-width: 990px) { 
    .dami__projects-container {
        flex-direction: column-reverse;
    }

    .dami__projects-container_groupA {
        margin: 2rem 0;
    }

    .dami__projects-container_groupA .dami__projects-container_article {
        width: 48%;
    }

    .dami__projects-container_groupA .dami__projects-container_article-image {
        height: 250px;
    }
}

@media screen and (max-width: 700px) { 
    .dami__projects-container_groupB {
        grid-template-columns: repeat(1, 1fr);
    }

    .dami__projects-container_groupA .dami__projects-container_article {
        width: 100%;
    }

    .dami__projects-heading h1 {
        font-size: 46px;
        line-height: 52px;
    }
}

@media screen and (max-width: 550px) { 
    .dami__projects-heading {
        margin-bottom: 2rem;
    }

    .dami__projects-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }
}
* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}

body {
    background: var(--color-bg);
}

a {
     color: unset;
     text-decoration: none;
}

.gradient__bg {
    /* ff 3.6+ */

    /* safari 5.1+,chrome 10+ */

    /* opera 11.10+ */

    /* ie 10+ */

    /* global 92%+ browsers support */
    background:radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
}

.gradient__text {
    background: var(--gradient-text);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.gradient__span {
    background: var(--gradient-span);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.section__padding {
    padding: 4rem 6rem;
}

.section__margin {
    margin: 4rem 6rem;
}

/**
* ----------------------------------------
* animation swirl-in-tr-fwd
* ----------------------------------------
*/
.swirl-in-tr-fwd {
	animation: swirl-in-tr-fwd 0.6s ease-out both;
}
    @keyframes swirl-in-tr-fwd {
    0% {
        transform: rotate(-540deg) scale(0);
        transform-origin: 100% 0%;
        opacity: 0;
    }
    100% {
        transform: rotate(0) scale(1);
        transform-origin: 100% 0%;
        opacity: 1;
    }
}

/**
* ----------------------------------------
* animation swirl-out-tr-bck
* ----------------------------------------
*/

.swirl-out-tr-bck {
	animation: swirl-out-tr-bck 0.6s ease-in both;
}
    @keyframes swirl-out-tr-bck {
    0% {
        transform: rotate(0) scale(1);
        transform-origin: 100% 0%;
        opacity: 1;
    }
    100% {
        transform: rotate(-540deg) scale(0);
        transform-origin: 100% 0%;
        opacity: 0;
    }
}
  
  
  
/**
* ----------------------------------------
*/

@media screen and (max-width: 700px) { 
    .section__padding {
        padding: 4rem;
    }

    .section__margin {
        margin: 4rem;
    }
}

@media screen and (max-width: 550px) { 
    .section__padding {
        padding: 4rem 2rem;
    }
    
    .section__margin {
        margin: 4rem 2rem;
    }
}
  
