  --gradient-span: linear-gradient(45deg, #ffff00 0%, #ffa500 100%);
  
  --color-bg: #040C18;
  --color-footer : #031B34;
  --color-project: #042c54;
  --color-text: #81AFDD;
  --color-subtext: #FF8A71;
}
