.dami__aboutme {
    display: flex;
    flex-direction: column;

    padding: 2rem;
    background: var(--color-footer);

    /* ff 3.6+ */
    background:-moz-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    /* safari 5.1+,chrome 10+ */
    background:-webkit-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    /* opera 11.10+ */
    background:-o-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    /* ie 10+ */
    background:-ms-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    /* global 92%+ browsers support */
    background:radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.dami__aboutme-feature {
    display: flex;
}

.dami__aboutme-feature .dami__features-container__feature {
    margin: 0;
}

.dami__aboutme-feature .dami__features-container__feature-text {
    max-width: 700px;
}

.dami__aboutme-heading {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 4rem 0 2rem;
}

.dami__aboutme-heading h1 {
    font-family: var(--font-family);
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
    max-width: 510px;
}

.dami__aboutme-heading button {
    min-width: 200px;
    min-height: 50px;
    background: linear-gradient(90deg, #AE67FA 0%, #F49867 97.4%);
    padding: 0 1rem;
    border: none;
    border-radius: 5px;
    outline: none;
    cursor: pointer;
}

.dami__aboutme-heading button p {
    font-family: var(--font-family);
    font-size: 16px;
    line-height: 30px;
    font-weight: 500;
    color: black;
}

.dami__aboutme-container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;

    margin-top: 2rem;
}

.dami__aboutme-container .dami__features-container__feature {
    flex: 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
}

.dami__aboutme-container .dami__features-container_feature-text {
    margin-top: 0.5rem;
}

@media screen and (max-width: 850px) {
    .dami__aboutme-heading {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .dami__aboutme-heading button {
        margin-top: 2rem;
    }
}

@media screen and (max-width: 650px) {
    .dami__aboutme-feature .dami__aboutme-container__feature {
        flex-direction: column;
        margin: 1rem 0;
    }

    .dami__aboutme-feature .dami__features-container_feature-text {
        margin-top: 0.5rem;
    }

    .dami__aboutme-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }

}

@media screen and (max-width: 350px) {
    .dami__aboutme-container .dami__features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}